import { StatusBar } from "expo-status-bar";
import { StyleSheet, Text, View } from "react-native-web";
import * as WebBrowser from "expo-web-browser";
import * as Google from "expo-auth-session/providers/google";
import AsyncStorage from "@react-native-async-storage/async-storage";
import React, { useState } from "react";
import { Button } from "@react-native-material/core";
import { log } from "../components/utils/log";
import { logger } from "react-native-logs";
import { Image } from "react-native";

WebBrowser.maybeCompleteAuthSession();

function UserLoginScreen() {
  const getAsyncStorageItem = async (key) => {
    return await AsyncStorage.getItem(key);
  };
  console.log(getAsyncStorageItem("user"));
  console.log(userInfo);

  const [userInfo, setUserInfo] = React.useState(getAsyncStorageItem("user"));
  const [request, response, promptAsync] = Google.useAuthRequest({
    androidClientId:
      "686308351661-aq3hp0o62s45q2gj6bhmtkni3v94q6d6.apps.googleusercontent.com",
    webClientId:
      "686308351661-stc5clqve4rt1r8cce5c1s3t0vqbtpug.apps.googleusercontent.com",
    iosClientId:
      "686308351661-vl8ave9iajmlnemr89hpkdi9g64cojf5.apps.googleusercontent.com",
  });
  function isNotPromiseOrNull(obj) {
    return (!obj || typeof obj.then !== "function") && obj !== null;
  }

  React.useEffect(() => {
    handleSigninWithGoogle();
  }, [response]);

  // React.useEffect(() => {
  //   deleteLocalStorage();
  // }, [userInfo]);

  async function handleSigninWithGoogle() {
    const user = await AsyncStorage.getItem("user");
    log.debug("handleSigninWithGoogle", "in");
    if (!user) {
      if (response?.type == "success") {
        await getUserInfo(response.authentication.accessToken);
      }
    } else {
      setUserInfo(JSON.parse(user));
    }
  }

  const showUserInfo = () => {
    console.log(userInfo);
    if (userInfo != null) {
      console.log(userInfo);
      console.log(userInfo.picture);
      return (
        <View
          style={{ flex: 1, alignItems: "center", justifyContent: "center" }}
        >
          <Text style={{ fontsize: 35, fontweight: "bold", marginBottom: 20 }}>
            Welcome
          </Text>
          <Image
            style={{ width: 200, height: 200, borderRadius: 50 }}
            source={{ uri: userInfo.picture }}
          />
          <Text>{userInfo.name}</Text>
          <Button
            title="Sign out"
            onPress={() => {
              setUserInfo(null);
              deleteLocalStorage();
            }}
          />
        </View>
      );
    }
  };

  const getUserInfo = async (token) => {
    if (!token) {
      log.debug("getUserInfo", "no token");
      return;
    }
    try {
      const response = await fetch(
        "https://www.googleapis.com/userinfo/v2/me",
        {
          headers: { Authorization: "Bearer " + token },
        }
      );
      const user = await response.json();
      await AsyncStorage.setItem("user", JSON.stringify(user));
      setUserInfo(user);
      console.log(user);
    } catch (error) {
      //
      logger.debug("getUserInfo", "FAIL");
    }
  };

  const deleteLocalStorage = () => {
    AsyncStorage.removeItem("user");
  };

  return (
    <View style={styles.container}>
      {!isNotPromiseOrNull(userInfo) && (
        <View>
          <Text> </Text>
          <Button title="Sign in Google" onPress={() => promptAsync()} />
          {/* <Button title="Delete local storage" onPress={deleteLocalStorage} /> */}
        </View>
      )}
      {isNotPromiseOrNull(userInfo) && showUserInfo()}

      {/* <Text> {JSON.stringify(userInfo,null,2)} </Text> */}

      {/* <StatusBar style="auto"/> */}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});

export default UserLoginScreen;
